import Header from "../../components/header";
import React from "react";
import TableStatus from "../../components/admin/tablestatus";
import SearchBox from "../../components/common/searchBox";
import { categoryData } from '../../constant/table_data'
import { Breadcrumb } from 'antd';


function AdminStatus() {

    const tags = [{ tagName: 123 }, { tagName: '田中' }, { tagName: '2024年2月' }];
    return (
        <>
            {/* <Header page={"adminscreen"} pagetitle={"管理画面 リクエスター管理 ホーム画面"} /> */}
            <section id="main-section">
                <div className="py-12 bg-header">
                    <Breadcrumb
                        className="p-5"
                        separator=">"
                        items={[
                            {
                                title: 'ADMINISTER_SCREEN',
                                href: '/design-management-screen'
                            },
                            {
                                title: 'MATCHINGSTAUTS_MANAGEMENT_SCREEN',
                            },
                        ]}
                    />
                    <SearchBox title="MATCHING　SERCH" tags={tags} />
                </div>
                <TableStatus categoryData={categoryData} />
            </section>
        </>
    );
}

export default AdminStatus;
